
import logo from './imgs/SunWestLogo.png';
import './App.css';
import React from 'react'; 
import { Button } from 'reactstrap';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,  
} from "react-router-dom";
import {Row,  Col,Form}from 'react-bootstrap';
import emailjs from 'emailjs-com';
const  Contact =() =>{

  function sendEmail(e){
    e.preventDefault();
    emailjs.sendForm(
      'service_z6pby1j',
      'template_9qep1fo',
      e.target,'user_w8UGktdue2sB3dmsdAGC2'
      ).then(res=>{
        console.log(res);
      }).catch(err=> console.log(err));
  }

  return (
      <div className=" bluebg">
         <h2 className="contacth2">Contact Us</h2>
          <div className="container border">
          <Form onSubmit={sendEmail}>
            <Row className="mb-3">
              <Form.Group as={Col} controlId="formGridEmail">
              <Form.Label></Form.Label>
                <Form.Control type="text" placeholder="Your First Name" nam="fname" />
              </Form.Group>

              <Form.Group as={Col} controlId="formGridPassword">
              <Form.Label></Form.Label>
              <Form.Control type="text" placeholder="Your First Name"  nam="lname"/>
              </Form.Group>
            </Row>
            <Row className="mb-3">
              <Form.Group as={Col} controlId="formGridEmail">
                <Form.Control type="emil" placeholder="Your Email" nam="email" />
              </Form.Group>

              <Form.Group as={Col} controlId="formGridPassword">
              <Form.Control type="text" placeholder="Your Phone Number" nam="phone" />
              </Form.Group>
            </Row>

          <Form.Group className="mb-3" controlId="formGridAddress1">
            <Form.Control placeholder="Your State " nam="State" />
          </Form.Group>

          <Form.Group className="mb-3" controlId="formGridAddress2">
            
            <Form.Control placeholder="Your Message "  nam="Message"  className="textheight"/>
          </Form.Group>


          <input type="submit" value='Send'/>
          </Form>
           
          </div>
 
          <div className="homecontact">
      <Link to="/">
         <Button color="inherit">Home</Button>
      </Link>
      </div>
    </div>
  );
}

export default Contact;
